@use './vars.scss' as vars;
.navlink{
  &.active{
    color: vars.$primary;
  }
}
.tweetsNavLink{
  &.active{
    color: vars.$primary;
  }
}
.post-text{
    font-size: 1.3rem;
    line-height: 1.5;

    color: vars.$light-1;
}
.post-name-text{
    font-size: 1.5rem;
    font-weight: bold;
    color: vars.$light-1;
}
.attribution-text{
    font-size: 0.8rem;
    color: vars.$light-1;
    position: absolute;
    bottom: 0;
    left: 0;
}

.text--medium--bold{
    font-size: 1.5rem !important;
    color: vars.$light-1 !important;
    font-weight: 600;
}
.text--medium--thin{
  font-size: 1.5rem !important;
  color: vars.$light-1 !important;
  font-weight: 500;
}
.text--big--primary--bold{
    font-size: 2.5rem !important;
    color: vars.$primary !important;
    font-weight: 700;
}
.text--medium-big--light--bold{
  font-size: 2rem !important;
  color: vars.$light-1 !important;
  font-weight: 600;
}
.text--medium--big--black--medium--bold{
    font-size: 2rem !important;
    color: black !important;
    font-weight: 600;

}

